import axios from 'axios'
import React, { useState } from 'react'
import { useEffect } from 'react'
import Navigation from "../components/Navigation"
import Product from '../components/Product'
import qrcode from "../components/qrcode.jpeg"
import contactimg from '../components/contact.png'
import Middlecontact from '../components/middlecontact'

function Contact() {
    const [contact, setContact] = useState({
        name: "",
        phone: "",
        email: "",
        address: "",
        message: ""
    })
    const [message, setmessage] = useState("")

    const changed = (e) => {
        setContact({
            ...contact,
            [e.target.name]: e.target.value
        })
    }

    const submit = (e) => {
        e.preventDefault();
        axios.post("https://alter-backend.herokuapp.com/contact", contact).then((res) => {
            setmessage(res.data.data.name);
            console.log(res.data.data);
        }).then(() => {
        })
        setTimeout(reset, 10000);

    }

    const reset = () => {
        setContact({
            name: "",
            phone: "",
            email: "",
            address: "",
            message: ""
        })
        setmessage("");
    }



    return (
        <div>
            <Navigation from={"Home"} link={" "} location={"Contact"} />
            {/* upper body of contact page */}
            <div className="contactdetails" style={{"backgroundImage":"url('https://res.cloudinary.com/shree8469175299/image/upload/v1656475111/backgrounds/Add_a_heading_roqmiy.png')"}}>
            {/* left side */}
                <div className="barcode container d-flex  align-items-center border-dark">
                    <img src={qrcode} alt="" height={300} width={300} className='image-fluid p-5' />
                    <div className='text text1 d-none d-sm-flex'>
                        <h1 className=''>&larr;</h1>
                    </div>
                    <div className=' w-100 text text1 d-flex d-sm-none'>
                        <h1 className=''>&uarr;</h1>
                    </div>
                    <div className='w-50 p-5 text'>
                        <h1>Orders</h1>
                        <h1>Are</h1>
                        <h1>Accepted</h1>
                        <h1>On</h1>
                        <h1>App</h1>
                        <h1>Only</h1>
                    </div>
                </div>
            {/* right side side */}
                <div className="text"> 
                    
                </div>
            </div>
<Middlecontact/>
            {/* contact page form */}
            <div className='p-5' style={{"backgroundImage":"url('https://res.cloudinary.com/shree8469175299/image/upload/o_10/v1655528966/becbdc8c-c271-4cd4-84a1-860e2632232f_o9dycu.jpg')"}}>
                <div className="container border p-3 form bg-white" >
                    <h1 className='text-center'>Contact form</h1>
                    <form>
                        <div className="form-group">
                            <label className='my-2'>Name</label>
                            <input type="text" name='name' value={contact.name} onChange={changed} className="form-control" placeholder="Enter Your Name" />
                        </div>
                        <div className="form-group">
                            <label className='my-2'>Phone No.</label>
                            <input type="Number" name='phone' value={contact.phone} onChange={changed} className="form-control" placeholder="Enter Your Phone No." />
                        </div>
                        <div className="form-group">
                            <label className='my-2'>Email address</label>
                            <input type="email" name='email' value={contact.email} onChange={changed} className="form-control" placeholder="Enter Your Email Address" />
                        </div>
                        <div className="form-group">
                            <label className='my-2'>Address</label>
                            <input type="text" name='address' value={contact.address} onChange={changed} className="form-control" placeholder="Enter Your Address" />
                        </div>
                        <div className="form-group">
                            <label className='my-2'>Query</label>
                            <input type="text" name='message' value={contact.message} onChange={changed} className="form-control" placeholder="Enter Your Quary" />
                        </div>
                        <div className='mt-3 d-flex align-items-center justify-content-between'>
                            <div className="btn btn-primary" onClick={submit}>Submit</div>
                            <div className="btn btn-secondary ms-3" onClick={reset}>Reset</div>
                        </div>
                    </form>
                    {message.length != 0 ? message != "success" ? (<div className='text-center'>
                        <span className='fw-bold'>{message}</span> your quary is <span className='fw-bold'>registered</span>
                    </div>) : "" : ""}
                </div>


            </div>
        </div>
    )
}

export default Contact